<template>
  <div class="hello">

    <div class="card-deck deck">
      <div class="col-sm-3" v-for='movie in movies'>
        <router-link :to='{name: "Film", params: { id: movie.id}}'>
            <div class="card product-item">
                <img class="card-img-top img-fluid" :src="movie.image" alt="Card image cap">
                <div class="card-body product-buttons">
                  <h5 class="re2 qwe">{{ movie.title }} ({{ movie.year }}) </h5>
                  <p class="re1">Жанры: {{ movie.genre1 }} {{ movie.genre2 }} {{ movie.genre3 }}</p>
                  <p class="re1">Рейтинг: {{ movie.rank }}</p>
                  <p class="re1 qwe">Время: {{ movie.long }} мин.</p>
                  <p class="re1">{{ movie.description }}</p>
                </div>
            </div>
        </router-link>
        </div>
    </div>
    <nav aria-label="Page navigation example">
      <ul class="pagination example justify-content-center">

        <li class="page-item">
          <a class="page-link" @click='backpage' v-on:click="loadRoom" href="#" tabindex="-1" aria-disabled="true">&laquo;</a>
        </li>
        <li class="page-item active"><a class="page-link " href="#">{{ page }} </a></li>
        <li class="page-item" @click='nextpage' v-on:click="loadRoom"><a  class="page-link" href="#">{{ page + 1 }}</a></li>

        <li class="page-item"><a class="page-link" href="#">...</a></li>
        <li class="page-item"><a class="page-link" @click='last' v-on:click="loadRoom"  href="#">{{ lastpage }}</a></li>
        <li class="page-item">
          <a class="page-link" @click='nextpage' v-on:click="loadRoom" href="#">&raquo;</a>
        </li>
      </ul>
    </nav>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'Movies',
  data () {
    return {
      searchHide: '',
      movies: null,
      page: 1,
      lastpage: '',
      url_prev: '',
      url_first: '',
      url_next: '',
      url_last: '',
      url: '',
      num: '2',
      active: false
    }
  },
  created () {
    this.loadRoom()
  },
  methods: {
    loadRoom () {
      this.url = '//mykinodom.ru/api/movies/?page=' + this.page
      axios
        .get(this.url)
        .then(response => (
          (this.movies = response.data.results),
          (this.lastpage = response.data.meta.pagination.pages),
          (this.page = response.data.meta.pagination.page)))
    },
    nextpage () {
      if (this.page < this.lastpage) {
        this.page = this.page + 1
      }
    },
    backpage () {
      if (this.page > 1) {
        this.page = this.page - 1
      }
    },
    last () {
      this.page = this.lastpage
    }
  }
}
</script>



