}
.row2 {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    align-content: center;
    margin-top: 5%;
}
.btn123 {
  width: 80%;
}
.left{
  float: right;
  cursor: pointer;
}

.btn{
  text-align: center;
}
.line {
  border-bottom: 1px solid black;
  margin-bottom: 3%;
}

.form {
  border-radius: 10px;
  margin-top: 2%;
  padding: 5%;
  background-color: white;
  box-shadow: 0 0 10px rgba(0,0,0,0.5);
}
body{
  background-color: #ECEFF1;
}

.card {
  margin: 20px;
}
.card-img-back {
  z-index: 412;
  position: sta;
}
.font{
  font-size: 14px;
}
.re{
  font-weight: bold;
}
</style>



