<template>
  <div class="home container">
    <form class="form" v-if='show'>
      <h2 class="ser">Подобрать<div class="left" @click='hideFilter'>&times;</div></h2>
      <div class="line"></div>
      <div class="row">
        <div class="col">
          Жанры
          <select v-model="genre" class="browser-default custom-select">
            <option selected></option>
            <option>Аниме</option>
            <option>Анимация</option>
            <option>Биография</option>
            <option>Боевик</option>
            <option>Вестерн</option>
            <option>Военный</option>
            <option>Детектив</option>
            <option>Документальный</option>
            <option>Драма</option>
            <option>Ужасы</option>
            <option>Исторический</option>
            <option >Криминальный</option>
            <option >Комедия</option>
            <option >Музыкальный</option>
            <option value="Анимация">Мульфильм</option>
            <option >Приключения</option>
            <option >Роман</option>
            <option >Семейный</option>
            <option >Спорт</option>
            <option >Триллер</option>
            <option >Фантастика</option>
            <option >Фэнтези</option>
            <option >Другой</option>
          </select>
        </div>
        <div class="col">
          Страна
          <select v-model='country' class="browser-default custom-select">
            <option selected></option>
            <option>Россия</option>
            <option>СССР</option>
            <option>Канада</option>
            <option>Великобритания</option>
            <option>США</option>
            <option>Австралия</option>
            <option>Бельгия</option>
            <option>Бразилия</option>
            <option>Германия</option>
            <option>Ирландия</option>
            <option>Испания</option>
            <option>Италия</option>
            <option>Мексика</option>
            <option>Франция</option>
            <option>Япония</option>
            <option>Другая</option>
          </select>
        </div>
        <div class="col">
          Год

          <div>
            <input v-model="year" class="custom-select" type="year" name="rank" maxlength="4" value="2019" step="1">
          </div>
        </div>
      </div>
      <div class="mar">
        <button @click='loadRoom4' type="button" class="btn btn-primary btn-lg btn-block">Подобрать</button>
      </div>
    </form>
    <div class="row row2">
      <div class="col ser3">
        <h5>1490 фильмов</h5>
      </div>
      <div class="col">
        <button v-if='!show' @click='showFilter'  class="btn btn-primary my-2 my-sm-0 btn123" type="submit">Подобрать</button>
      </div>
      <div class="col ser2">
        <select @click='loadRoom2' class="browser-default custom-select custom-select2">
          <option selected><div>{{ sort }}</div></option>
        </select>
      </div>
    </div>
    <Movies v-if='!show2' />
    <div v-if='show2' >
      <div class="card-deck deck">
        <div class="col-sm-3" v-for='movie in movies'>
          <router-link :to='{name: "Film", params: { id: movie.id}}'>
            <div class="card product-item">
                <img class="card-img-top img-fluid" :src="movie.image" alt="Card image cap">
                <div class="card-body product-buttons">
                  <h5 class="re2 qwe">{{ movie.title }} ({{ movie.year }}) </h5>
                  <p class="re1">Жанры: {{ movie.genre1 }} {{ movie.genre2 }} {{ movie.genre3 }}</p>
                  <p class="re1">Рейтинг: {{ movie.rank }}</p>
                  <p class="re1 qwe">Время: {{ movie.long }} мин.</p>
                  <p class="re1">{{ movie.description }}</p>
                </div>
            </div>
          </router-link>
          </div>
      </div>
      <nav aria-label="Page navigation example">
        <ul class="pagination example justify-content-center">
          <li class="page-item">
            <a class="page-link" @click='backpage' v-on:click="loadRoom" href="#" tabindex="-1" aria-disabled="true">&laquo;</a>
          </li>
          <li class="page-item active"><a class="page-link " href="#">{{ page }} </a></li>
          <li class="page-item" @click='nextpage' v-on:click="loadRoom"><a  class="page-link" href="#">{{ page + 1 }}</a></li>
          <li class="page-item"><a class="page-link" href="#">...</a></li>
          <li class="page-item"><a class="page-link" @click='last' v-on:click="loadRoom"  href="#">{{ lastpage }}</a></li>
          <li class="page-item">
            <a class="page-link" @click='nextpage' v-on:click="loadRoom" href="#">&raquo;</a>
          </li>
        </ul>
      </nav>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import Movies from '@/components/Movies.vue'
export default {
  name: 'filters',
  components: {
    Movies,
  },
  data () {
    return {
      show: false,
      show2: false,
      country: '',
      genre: '',
      year: '',
      movies: null,
      page: 1,
      lastpage: '',
      url_prev: '',
      url_first: '',
      url_next: '',
      url_last: '',
      url: '',
      num: '2',
      ord: '-rank',
      sort: 'По дате добавления',
    }
  },
  methods: {
    showFilter () {
      this.show = true
    },
    hideFilter () {
      this.show = false

    },
    loadRoom () {
      this.show2 = true
      this.url = '//mykinodom.ru/api/movies/?search='+ this.country+ '+' + this.genre + '+' + this.year+ '&ordering=' +this.ord + '&page=' + this.page
      axios
        .get(this.url)
        .then(response => (
          (this.movies = response.data.results),
          (this.lastpage = response.data.meta.pagination.pages),
          (this.page = response.data.meta.pagination.page)))
    },
    loadRoom4 () {
      this.show2 = true
      this.page = 1
      this.url = '//mykinodom.ru/api/movies/?search='+ this.country+ '+' + this.genre + '+' + this.year+ '&ordering=' +this.ord + '&page=' + this.page
      axios
        .get(this.url)
        .then(response => (
          (this.movies = response.data.results),
          (this.lastpage = response.data.meta.pagination.pages),
          (this.page = response.data.meta.pagination.page)))
    },
    loadRoom2 () {
      this.sort = 'По рейтингу'
      this.show2 = true
      this.url = '//mykinodom.ru/api/movies/?ordering=' + this.ord + '&page=' + this.page
      axios
        .get(this.url)
        .then(response => (
          (this.movies = response.data.results),
          (this.lastpage = response.data.meta.pagination.pages),
          (this.page = response.data.meta.pagination.page)))
    },
    nextpage () {
      if (this.page < this.lastpage) {
        this.page = this.page + 1
      }
    },
    backpage () {
      if (this.page > 1) {
        this.page = this.page - 1
      }
    },
    last () {
      this.page = this.lastpage
    }
  }
}
</script>



