import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/film/:id',
      name: 'Film',
      component: () => import(/* webpackChunkName: "about" */ './views/Film.vue')
    },
    {
      path: '/Serials/',
      name: 'Serials',
      component: () => import(/* webpackChunkName: "about" */ './views/Serials.vue')
    },
    {
      path: '/Serials/:id',
      name: 'Serial',
      component: () => import(/* webpackChunkName: "about" */ './views/Serial.vue')
    },
    {
      path: '/Anime/',
      name: 'Anime',
      component: () => import(/* webpackChunkName: "about" */ './views/Animes.vue')
    },
    {
      path: '/Anime/:id',
      name: 'AnimeView',
      component: () => import(/* webpackChunkName: "about" */ './views/AnimeView.vue')
    },
    {
      path: '/top/',
      name: 'Top',
      component: () => import(/* webpackChunkName: "about" */ './views/Top.vue')
    },
    {
      path: '/top/top250',
      name: 'top250',
      component: () => import(/* webpackChunkName: "about" */ './views/top250.vue')
    },
    {
      path: '/top/top250/:id',
      name: 'Film250',
      component: () => import(/* webpackChunkName: "about" */ './views/Film250.vue')
    },
    {
      path: '/top/BestSerials',
      name: 'BestSerials',
      component: () => import(/* webpackChunkName: "about" */ './views/BestSerials.vue')
    },
    {
      path: '/top/BestSerials/:id',
      name: 'BestSerial',
      component: () => import(/* webpackChunkName: "about" */ './views/BestSerial.vue')
    }
  ]
})



